.header{
    background-color: $verde;
    .contenido-header{
        padding: $separacion / 2 0;
        
        @include tablet{
            display: flex;
            justify-content: space-between;
            align-items: center;             
        }
    }
    
    h1{
        color: $blanco;
        margin-bottom: 1rem;
        text-align: center;
        @include tablet{
            margin-bottom: 0;
        }
    }

    &.fijo{
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .1rem #444;
    }
}

.navegacion-principal{
    display: flex;
    gap: 4rem;
    font-weight: $regular;
    justify-content: center;
    a{
        font-size: 2rem;
        color: $blanco;
        &:hover{
            color: $amarillo;
            text-decoration: underline;
        }
    }
}