html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

body.fijar-body {
  overflow: hidden;
}

p {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  color: #333652;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
}

h1, h2, h3 {
  margin: 0;
}

.bg-amarillo {
  background-color: #FAD02C;
}

.bg-verde {
  background-color: #09dbb8;
}

.header {
  background-color: #09dbb8;
}

.header .contenido-header {
  padding: 2.5rem 0;
}

@media (min-width: 760px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.header h1 {
  color: #f8f8f8;
  margin-bottom: 1rem;
  text-align: center;
}

@media (min-width: 760px) {
  .header h1 {
    margin-bottom: 0;
  }
}

.header.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 .3rem .1rem #444;
}

.navegacion-principal {
  display: flex;
  gap: 4rem;
  font-weight: 400;
  justify-content: center;
}

.navegacion-principal a {
  font-size: 2rem;
  color: #f8f8f8;
}

.navegacion-principal a:hover {
  color: #FAD02C;
  text-decoration: underline;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}

.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #b91bd6;
  background: linear-gradient(63deg, rgba(185, 27, 214, 0.654499) 0%, rgba(195, 130, 27, 0.466824) 25%, rgba(214, 27, 101, 0.522847) 50%, rgba(132, 232, 233, 0.119485) 75%, rgba(0, 0, 0, 0.665704) 100%);
}

.video video {
  display: block;
  width: 100%;
  overflow: hidden;
}

.video .contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
  color: #f8f8f8;
}

.video .contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 7rem;
}

.video .contenido-video span {
  font-size: 2rem;
  background-color: #eb2c6f;
  width: 36rem;
  padding: 1rem;
  text-align: center;
}

.info {
  padding: 5rem 0;
}

@media (min-width: 480px) {
  .info {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    column-gap: 5rem;
  }
}

@media (min-width: 760px) {
  .info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
  }
}

.info__contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

.info__contenido h2 {
  font-size: 3rem;
}

.info__contenido .fecha {
  color: #eb2c6f;
  font-weight: 700;
}

.info__contenido .texto {
  font-size: 2.5rem;
  line-height: 1.5;
}

.lineup {
  background-color: #eb2c6f;
  padding: 5rem 0;
}

.lineup h3, .lineup p {
  text-align: center;
  color: #f8f8f8;
}

.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

.escenario {
  border-radius: .6rem;
  padding-top: 2.5rem;
}

@media (min-width: 760px) {
  .escenario-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
  }
}

.escenario-contenedor .nombre-escenario {
  color: #333652;
  font-size: 2rem;
  font-weight: 300;
}

.escenario-contenedor .nombre-escenario:after {
  content: '';
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  padding: 0;
  list-style: none;
}

.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}

.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(2n +1) {
  background-color: #07aa8f;
}

.bg-amarillo .calendario li:nth-child(2n +1) {
  background-color: #edbe06;
}

.galeria {
  padding: 5rem 0;
}

.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5rem;
}

@media (min-width: 760px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 2.5rem;
  }
}

@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 2.5rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  background-color: #f8f8f8;
  color: #333652;
  display: grid;
  place-items: center;
  font-weight: 900;
  border-radius: 50%;
}

.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}

.boletos h3 {
  text-align: center;
  color: #f8f8f8;
  font-size: 4rem;
}

.boletos .contenedor-boletos {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5rem;
}

.boletos .plan p {
  color: #f8f8f8;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pase {
  width: 100%;
  border-spacing: 0;
}

.pase thead {
  background-color: #eb2c6f;
}

.pase thead th {
  padding: 1.25rem;
  color: #f8f8f8;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2rem;
}

tbody {
  background-color: #f8f8f8;
}

tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #e1e1e1;
}

tbody tr:last-of-type tr {
  border: none;
}

tfoot {
  background-color: #A16AE8;
}

tfoot td {
  padding: 1.25rem;
  text-align: center;
  font-size: 3rem;
  color: #f8f8f8;
}

.footer {
  background-color: #eb2c6f;
  text-align: center;
  padding: 1.25rem;
}

.footer p {
  color: #f8f8f8;
  margin: 0;
}
